* {
    font-family: sans-serif;
}

.container {
    max-width: 1200px;
    margin: 0 auto;
}

.header {
    display: flex;
    justify-content: space-between;
    margin-top: 35px;
}

.header-nav {
    display: flex;
    justify-content: space-between;
    width: 60%;
}

a {
    text-decoration: none;
    font-size: 20px;
    font-weight: 700;
}

.dn {
    display: none;
}

.header-select {
    font-size: 18px;
    border: 0;
    margin-bottom: 15px;
}

.main {
    display: flex;
    justify-content: space-between;
    margin-top: 45px;
}

.main-heading {
    font-size: 58px;
    max-width: 550px;
}

.main-text {
    font-size: 24px;
    color: #9dd03c;
    max-width: 470px;
}

.main-btn {
    padding: 20px 55px;
    background-color: rgb(251, 48, 48);
    color: #fff;
    border: 0;
    border-radius: 5px;
    font-size: 20px;
    cursor: pointer;
}

.wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.trusted-text {
    font-size: 23px;
}

.line {
    width: 40%;
    height: 2px;
    background-color: gray;
}

.need {
    display: flex;
    justify-content: space-between;
    margin-top: 120px;
}

.wrap {
    max-width: 400px;
    font-size: 30px;
}

ul {
    font-size: 20px;
    color: dimgray;
}

li {
    margin-bottom: 20px;
}

.analysis {
    margin-top: 150px;
}

.analysis-heading {
    font-size: 50px;
    text-align: center;
}

.block {
    width: 30%;
    text-align: center;
    font-size: 20px;
}

.wrap3 {
    justify-content: center;
}

input {
    margin-left: 30px;
    font-size: 40px;
    height: 60px;
    border-radius: 5px;
    border: 3px solid green;
}

.i1 {
    transform: translateX(-250px);
}

.i2 {
    transform: translateX(250px);
}

.last {
    margin-bottom: 50px;
}

.create {
    padding: 20px 30px;
    background-color: rgb(96, 96, 236);
    margin-left: 10px;
    font-size: 22px;
    color: #fff;
    border: 0;
    border-radius: 5px;
    cursor: pointer;
}

.footer-line {
    position: relative;
    margin-top: 70px;
    margin-bottom: 20px;
    display: block;
    width: 100%;
    height: 2px;
    background-color: rgb(200, 184, 184);
}

.gray {
    color: gray;
    font-size: 18px;
}

.latest {
    width: 65%;
}